.search-form-container {
  background-color: #23449f;
  padding: 10px 20px;
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 1px 2px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.search-form {
  max-width: 420px;
  width: 100%;
  border-radius: 10px;
  margin-left: auto;
  margin-right: auto;
  transition: outline .25s linear;
  display: flex;
  overflow: hidden;
}

.search-form:focus-within {
  outline: 2px solid #2196f3;
}

.search-form input {
  height: 40px;
  width: 100%;
  border: none;
  outline: none;
  padding: 10px;
  display: block;
}

.search-form button {
  min-width: 40px;
  cursor: pointer;
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.icon-btn {
  fill: #302d2d;
}

